

.SliderContainer {
    background-color: #f3f8fe;
    position: relative;
}

.slider_title {
    font-size: 2vw;
    /* color: tomato; */
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 1vw;
    font-family: 'Allerta Stencil', sans-serif;
    background-color: #f3f8fe;
}

.title-desc {
    font-size: vw;
    background-color: #f3f8fe;
    text-align: center;
}

.card-wrapper {
    position: relative;
    width: 100%;
    padding: 0 10px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.card-wrapper:active,
.card-wrapper:hover,
.card-wrapper:focus {
    outline: none;
    border: none;
}

.card {
    width: 100%;
    height: 450px;
    border-radius: 16px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
}

.card .card-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70%;
    z-index: 2;
    background-color: #000;
    -webkit-transition: .5s;
    transition: .5s;
}

.card-image>h2 {
    font-size: 3vh;
}

.card .card-image img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
}

.card:hover img {
    opacity: .4;
    -webkit-transition: .5s;
    transition: .5s;
}
.slick-slider-cart-rating{
    display: flex;
    justify-content:center;
}

.card:hover .card-image {
    -webkit-transform: translateY(-100px);
    transform: translateY(-100px);
    -webkit-transition: all .9s;
    transition: all .9s;
}

.social-icons {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index: 3;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.social-icons li {
    list-style: none;
}

.social-icons li a {
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    background: #fff;
    font-size: 23px;
    color: #333;
    font-weight: bold;
    margin: 0 6px;
    -webkit-transition: .4s;
    transition: .4s;
    -webkit-transform: translateY(200px);
    transform: translateY(200px);
    opacity: 0;
}

.card:hover .social-icons li a {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
}

.social-icons li a:hover {
    background: #000;
    -webkit-transition: .2s;
    transition: .2s;
}

.social-icons li a:hover .fa {
    color: #fff;
}

.social-icons li a .fa {
    -webkit-transition: .8s;
    transition: .8s;
}

.social-icons li a .fa:hover {
    -webkit-transform: rotateY(360deg);
    transform: rotateY(360deg);
    color: #fff;
}

.card:hover li:nth-child(1) a {
    -webkit-transition-delay: .1s;
    transition-delay: .1s;
}

.card:hover li:nth-child(2) a {
    -webkit-transition-delay: .2s;
    transition-delay: .2s;
}

.card:hover li:nth-child(3) a {
    -webkit-transition-delay: .3s;
    transition-delay: .3s;
}

.card:hover li:nth-child(4) a {
    -webkit-transition-delay: .4s;
    transition-delay: .4s;
}

.details {
    position: absolute;
    bottom: 400px;
    left: 0;
    background: #fff;
    width: 100%;
    height: 120px;
    z-index: 1;
    padding: 10px;
    -webkit-transition: .6s;
    transition: .6s;
}

.details h2 {
    font-size: 17px;
    padding: 0;
    text-align: center;
}

.details h2 .job-title {
    font-size: 14px;
    line-height: 2;
    color: #333;
    font-weight: 300;
    display: block;
}

.card:hover .details {
    bottom: 0;
}

.carousel .slick-dots {
    bottom: -2.5vw;
    background-color: #f8f8f8;
}

.carousel .slick-dots li button {
    width: 20px;
    height: 20px;
    border: 1px solid #fff;
    border-radius: 20px;
}

.carousel .slick-dots li button:before {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    content: "";
    text-align: center;
    opacity: .5;
    color: #000;
    background-color: #fff;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.carousel .slick-dots li.slick-active button,
.carousel .slick-dots li:hover button {
    border-color: #8cd0e3;
}

.carousel .slick-dots li.slick-active button:before,
.carousel .slick-dots li:hover button:before {
    background-color: #8cd0e3;
    opacity: 1;
}

.carousel .slick-prev,
.carousel .slick-next {
    width: 50px;
    height: 50px;
    background-color: #fff;
}

.carousel .slick-prev:hover,
.carousel .slick-prev:focus,
.carousel .slick-next:hover,
.carousel .slick-next:focus {
    color: #fff;
    outline: none;
    background: #fff;
}

.carousel .slick-prev:before,
.carousel .slick-next:before {
    color: #000;
    font-family: "Raleway", sans-serif;
}

.carousel .slick-next {
    right: -55px;
}

.carousel .slick-prev {
    left: -55px;
}


